.my-work .row {
  min-height: 80vh;
  min-height: 600px;
  justify-content: center;
}

.my-work h1 {
  color: black;
}

.card {
  display: inline-flex;
  flex-direction: column;
  width: 23rem;
  height: 30rem;
  margin-right: 20px;
  margin-top: 4rem;
  padding: 0rem 0rem 4rem 0rem;
  border: 2px solid #E8F0FF;
  border-radius: 8px;
  text-decoration: none;
  overflow: hidden;
}

.card__list-item-img {
  height: 0;
  padding-bottom: 50%;
  margin-bottom: 1rem;
  position: relative;
  overflow: hidden;
  display: inline-block;
  width: 100%;
  height: 100%;
}

.card__list-item-img img {
  position: absolute;
  object-fit: cover;
  max-width: 100%;
  height: auto;
}

.card__content-title {
  display: block;
  margin: 0 auto;
  margin-top: 1rem;
  margin-bottom: 16px;
  width: 90%;
  height: 44.8px;

  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  line-height: 1.5;
  color: #32324B;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__content-tech {
  padding: 1rem;
}

.card__content-tech-text {
  color: #9C9CA7;
  display: inline-block;
  margin-bottom: 1rem;
}

.card__content-tech-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.card__content-tech-item {
  margin: 0.4rem 0.5rem;
  padding: 2px 8px;
  background-color: #F0F0F0;
  color: #32324B;
}


.card__content-tech-item:not(:last-child) {
  margin-right: 8px;
}