.navigation {
  display: block;
  width: 100%;
  padding: 1rem 0rem;
  box-shadow: 0 20px 35px -14px rgba(199, 207, 219, 0.5)
}

.links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.links div>a {
  margin: 0rem 2rem;
  padding: 1rem;
  position: relative;
  z-index: 0;
  text-decoration: none;
  color: #313131;
  font-family: inherit;
  font-weight: 400;
  font-size: 1.2rem;
  letter-spacing: 1px;
  cursor: pointer;
}

/* Circle behind */
div.circleBehind a:before,
div.circleBehind a:after {
  position: absolute;
  top: 22px;
  left: 50%;
  width: 50px;
  height: 50px;
  border: 4px solid #0277bd;
  transform: translateX(-50%) translateY(-50%) scale(0.8);
  border-radius: 50%;
  background: transparent;
  content: '';
  opacity: 0;
  transition: all 0.3s;
  z-index: -1;
}

div.circleBehind a:after {
  border-width: 2px;
  transition: all 0.4s;
}

div.circleBehind a:hover:before {
  opacity: 0.2;
  transform: translateX(-50%) translateY(-50%) scale(1);
}

div.circleBehind a:hover:after {
  opacity: 0.2;
  transform: translateX(-50%) translateY(-50%) scale(1.3);
}

/* Active class */
.active a {
  color: dodgerblue !important;
  font-weight: 600 !important;
}