@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
.cover-photo {
  background-image: url('https://res.cloudinary.com/dzhybmnuw/image/upload/v1595316417/cover-photo_t7vxav.jpg');
  background-size: cover;
  object-fit: cover;
  height: 50vh;
  min-height: 400px;
  position: relative;

  color: #ffffff;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  background: linear-gradient(180deg,
      transparent 0,
      rgba(0, 0, 0, 0.03) 8%,
      rgba(0, 0, 0, 0.11) 21%,
      rgba(0, 0, 0, 0.61) 78%,
      rgba(0, 0, 0, 0.7) 95%,
      rgba(0, 0, 0, 0.7));
}

.profile-wrapper {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile {
  width: 100%;
  text-align: center;
}

.avatar {
  margin: 0 auto;
  min-width: 56px;
  min-height: 56px;
  max-width: 150px;
  max-height: 150px;
}

.avatar img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  object-fit: cover;
}

.profile .name {
  line-height: 1;
  font-size: 2rem;
  letter-spacing: 2px;
  font-weight: 400;
  margin-top: 1.5rem;
}

.profile .role {
  margin-top: 0.8rem;
  font-size: 1.5rem;
  font-weight: 400;
  letter-spacing: 1px;
}

.info {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.info .info-detail {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0.5rem;
  padding: 0.5rem 1rem;
  border-left: 1px solid #ffffff;
}

.info-detail .svg-inline--fa {
  fill: #ffffff;
  width: 24px;
  height: 24px;
}

.info-detail a {
  text-decoration: none;
  color: #ffffff;
  margin-left: 0.5rem;
}

.info-detail:hover a {
  text-decoration: underline;
}

.info-detail span {
  margin-left: 0.5rem;
}

.info>div:nth-child(1) {
  border: none;
}
.navigation {
  display: block;
  width: 100%;
  padding: 1rem 0rem;
  box-shadow: 0 20px 35px -14px rgba(199, 207, 219, 0.5)
}

.links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.links div>a {
  margin: 0rem 2rem;
  padding: 1rem;
  position: relative;
  z-index: 0;
  text-decoration: none;
  color: #313131;
  font-family: inherit;
  font-weight: 400;
  font-size: 1.2rem;
  letter-spacing: 1px;
  cursor: pointer;
}

/* Circle behind */
div.circleBehind a:before,
div.circleBehind a:after {
  position: absolute;
  top: 22px;
  left: 50%;
  width: 50px;
  height: 50px;
  border: 4px solid #0277bd;
  transform: translateX(-50%) translateY(-50%) scale(0.8);
  border-radius: 50%;
  background: transparent;
  content: '';
  opacity: 0;
  transition: all 0.3s;
  z-index: -1;
}

div.circleBehind a:after {
  border-width: 2px;
  transition: all 0.4s;
}

div.circleBehind a:hover:before {
  opacity: 0.2;
  transform: translateX(-50%) translateY(-50%) scale(1);
}

div.circleBehind a:hover:after {
  opacity: 0.2;
  transform: translateX(-50%) translateY(-50%) scale(1.3);
}

/* Active class */
.active a {
  color: dodgerblue !important;
  font-weight: 600 !important;
}
.about-me .row {
  min-height: 80vh;
  min-height: 600px;
  padding: 2rem 0rem 4rem 0rem;
  margin: 0rem 0rem 4rem 0rem;
}


.personal-stuff {
  padding: 4rem 0rem 4rem 5rem;
}

.personal-stuff .description {
  max-width: 90%;
  text-align: left;
  font-family: inherit;
  font-size: 2rem;
  font-weight: 200;
  line-height: 1.6;
}

.personal-stuff .btn-primary {
  margin-left: 0rem !important;
}

/* Technical */
.technical {
  /* background-color: dodgerblue; */
  padding: 4rem 0rem 4rem 2rem;

  display: flex;
  flex-wrap: wrap;
}

.technical-title {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.technical-logos {
  max-width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.logo {
  display: flex;
  flex-direction: column;
  margin: 0rem 0rem 1rem 0rem;
  width: 200px;
  text-align: center;
}


.logo .svg-inline--fa {
  height: 80px !important;
  margin: 1rem 2rem;
  fill: dodgerblue;
}
.my-work .row {
  min-height: 80vh;
  min-height: 600px;
  justify-content: center;
}

.my-work h1 {
  color: black;
}

.card {
  display: inline-flex;
  flex-direction: column;
  width: 23rem;
  height: 30rem;
  margin-right: 20px;
  margin-top: 4rem;
  padding: 0rem 0rem 4rem 0rem;
  border: 2px solid #E8F0FF;
  border-radius: 8px;
  text-decoration: none;
  overflow: hidden;
}

.card__list-item-img {
  height: 0;
  padding-bottom: 50%;
  margin-bottom: 1rem;
  position: relative;
  overflow: hidden;
  display: inline-block;
  width: 100%;
  height: 100%;
}

.card__list-item-img img {
  position: absolute;
  object-fit: cover;
  max-width: 100%;
  height: auto;
}

.card__content-title {
  display: block;
  margin: 0 auto;
  margin-top: 1rem;
  margin-bottom: 16px;
  width: 90%;
  height: 44.8px;

  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  line-height: 1.5;
  color: #32324B;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__content-tech {
  padding: 1rem;
}

.card__content-tech-text {
  color: #9C9CA7;
  display: inline-block;
  margin-bottom: 1rem;
}

.card__content-tech-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.card__content-tech-item {
  margin: 0.4rem 0.5rem;
  padding: 2px 8px;
  background-color: #F0F0F0;
  color: #32324B;
}


.card__content-tech-item:not(:last-child) {
  margin-right: 8px;
}
.footer-wrapper {
  padding: 2rem 0rem 1rem 0rem;
  background-color: #333333;
  display: block;
  text-align: center;
}

.footer-wrapper div {
  margin-bottom: 0.5rem;
}

.footer-wrapper div a {
  text-decoration: none;
  margin: 1rem 4rem;
}

.footer-wrapper .svg-inline--fa {
  height: 50px;
  fill: #ffffff;
  cursor: pointer;
}

.footer-wrapper .svg-inline--fa:hover {
  fill: dodgerblue;
  transition: all 300ms ease-in;
}

.footer-wrapper span {
  color: #ffffff;
  letter-spacing: 2px;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

