.about-me .row {
  min-height: 80vh;
  min-height: 600px;
  padding: 2rem 0rem 4rem 0rem;
  margin: 0rem 0rem 4rem 0rem;
}


.personal-stuff {
  padding: 4rem 0rem 4rem 5rem;
}

.personal-stuff .description {
  max-width: 90%;
  text-align: left;
  font-family: inherit;
  font-size: 2rem;
  font-weight: 200;
  line-height: 1.6;
}

.personal-stuff .btn-primary {
  margin-left: 0rem !important;
}

/* Technical */
.technical {
  /* background-color: dodgerblue; */
  padding: 4rem 0rem 4rem 2rem;

  display: flex;
  flex-wrap: wrap;
}

.technical-title {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.technical-logos {
  max-width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.logo {
  display: flex;
  flex-direction: column;
  margin: 0rem 0rem 1rem 0rem;
  width: 200px;
  text-align: center;
}


.logo .svg-inline--fa {
  height: 80px !important;
  margin: 1rem 2rem;
  fill: dodgerblue;
}