  }

  /* 7/12 */
  .large-col-7 {
    width: 58.33%;
  }

  /* 8/12 */
  .large-col-8 {
    width: 66.66%;
  }

  /* 9/12 */
  .large-col-9 {
    width: 75%;
  }

  /* 10/12 */
  .large-col-10 {
    width: 83.33%;
  }

  /* 11/12 */
  .large-col-11 {
    width: 91.66%;
  }

  /* 12/12 */
  .large-col-12 {
    width: 100%;
  }
}


/* viewport <= 1000px */
@media screen and (max-width: 1000px) {
  * {
    font-size: 1em;
  }

  .medium-col-0 {
    width: 0;
  }

  /* 1/12 */
  .medium-col-1 {
    width: 8.33%;
