.cover-photo {
  background-image: url('https://res.cloudinary.com/dzhybmnuw/image/upload/v1595316417/cover-photo_t7vxav.jpg');
  background-size: cover;
  object-fit: cover;
  height: 50vh;
  min-height: 400px;
  position: relative;

  color: #ffffff;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  background: linear-gradient(180deg,
      transparent 0,
      rgba(0, 0, 0, 0.03) 8%,
      rgba(0, 0, 0, 0.11) 21%,
      rgba(0, 0, 0, 0.61) 78%,
      rgba(0, 0, 0, 0.7) 95%,
      rgba(0, 0, 0, 0.7));
}

.profile-wrapper {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile {
  width: 100%;
  text-align: center;
}

.avatar {
  margin: 0 auto;
  min-width: 56px;
  min-height: 56px;
  max-width: 150px;
  max-height: 150px;
}

.avatar img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  object-fit: cover;
}

.profile .name {
  line-height: 1;
  font-size: 2rem;
  letter-spacing: 2px;
  font-weight: 400;
  margin-top: 1.5rem;
}

.profile .role {
  margin-top: 0.8rem;
  font-size: 1.5rem;
  font-weight: 400;
  letter-spacing: 1px;
}

.info {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.info .info-detail {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0.5rem;
  padding: 0.5rem 1rem;
  border-left: 1px solid #ffffff;
}

.info-detail .svg-inline--fa {
  fill: #ffffff;
  width: 24px;
  height: 24px;
}

.info-detail a {
  text-decoration: none;
  color: #ffffff;
  margin-left: 0.5rem;
}

.info-detail:hover a {
  text-decoration: underline;
}

.info-detail span {
  margin-left: 0.5rem;
}

.info>div:nth-child(1) {
  border: none;
}